"use client";

import { useEffect, useState } from "react";
import { useParams } from "next/navigation";
import Image from "next/image";
import Link from "next/link";

interface Product {
  _id: string;
  name: string;
  description: string;
  price: number;
  discountPrice?: number;
  stock: number;
  unit: string;
  deliveryTime: string;
  category: string;
  subcategory: string;
  returnPolicy: string;
  paymentMode: string;
  sellerName: string;
  contact: string;
  location: string;
  images: string[];
}

export default function ProductDetailPage() {
  const params = useParams();
  const productId = params.productId as string;
  const [product, setProduct] = useState<Product | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  const [selectedImage, setSelectedImage] = useState<string | null>(null);

  useEffect(() => {
    async function fetchProduct() {
      try {
        const res = await fetch(
          `http://localhost:5000/api/products/${productId}`
        );
        if (!res.ok) {
          throw new Error("Failed to fetch product details");
        }
        const data = await res.json();
        setProduct(data.product);
        setSelectedImage(data.product.images[0]); 
      } catch (err: any) {
        setError(err.message || "Something went wrong");
      } finally {
        setLoading(false);
      }
    }
    if (productId) {
      fetchProduct();
    }
  }, [productId]);

  if (loading)
    return (
      <div className="flex justify-center items-center h-screen">
        <div className="w-12 h-12 border-4 border-gray-300 border-t-blue-500 rounded-full animate-spin"></div>
      </div>
    );
  if (error) return <p className="text-center text-red-500">Error: {error}</p>;
  if (!product) return <p className="text-center">No product found.</p>;

  return (
    <div className="container mx-auto p-4">
      <Link href={`/shop/${product.category}/${product.subcategory}`} className="text-blue-600">
        ‚Üê Back to Products
      </Link>

      <div className="flex flex-col md:flex-row mt-4">
        {/* Left Column - Thumbnails */}
        <div className="hidden md:flex flex-col space-y-2 mr-4">
          {product.images.map((img, index) => (
            <Image
              key={index}
              src={`http://localhost:5000${img}`}
              alt={`Thumbnail ${index}`}
              width={80}
              height={80}
              className={`cursor-pointer rounded-md border-2 ${
                selectedImage === `http://localhost:5000${img}`
                  ? "border-blue-500"
                  : "border-transparent"
              }`}
              onClick={() => setSelectedImage(`http://localhost:5000${img}`)}
            />
          ))}
        </div>

        {/* Right Side - Main Image (Default First Image) */}
        <div className="w-full md:w-1/2 relative h-96">
          <Image
            src={selectedImage || "/placeholder.svg"}
            alt={product.name}
            layout="fill"
            objectFit="cover"
            className="rounded-md transition-opacity"
          />
        </div>

        {/* Product Details */}
        <div className="w-full md:w-1/2 md:pl-8 mt-4 md:mt-0">
          <h1 className="text-3xl font-bold">{product.name}</h1>
          <p className="mt-2 text-lg">{product.description}</p>
          <div className="mt-4 text-2xl font-semibold">
            ${product.price.toFixed(2)}
            {product.discountPrice && (
              <span className="ml-2 text-xl line-through text-gray-500">
                ${product.discountPrice.toFixed(2)}
              </span>
            )}
          </div>
          <p className="mt-2">
            <strong>Stock:</strong> {product.stock} {product.unit}
          </p>
          <p className="mt-2">
            <strong>Delivery Time:</strong> {product.deliveryTime}
          </p>
          <p className="mt-2">
            <strong>Return Policy:</strong> {product.returnPolicy}
          </p>
          <p className="mt-2">
            <strong>Payment Mode:</strong> {product.paymentMode}
          </p>
          <p className="mt-2">
            <strong>Seller:</strong> {product.sellerName}
          </p>
          <p className="mt-2">
            <strong>Contact:</strong> {product.contact}
          </p>
          <p className="mt-2">
            <strong>Location:</strong> {product.location}
          </p>
        </div>
      </div>
    </div>
  );
}
